@use '../abstracts/' as *;

body {
  height: 100vh;
  width: 100%;
  display: grid;
  place-items: center;
  background-color: $theme-bg;
  color: $theme-text-primary;
  font-family: $fonts;
  padding: 0 rem(24);
}

hr {
  border: 0;
  height: rem(1);
  background-color: $theme-divider;
}
