html {
  font-size: 100%;
  scroll-behavior: smooth;
}

*,
::after,
::before {
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
}

ul,
ol li {
  list-style-type: none;
}

a,
a:visited,
a:hover {
  color: var(--color-white);
  text-decoration: none;
  text-transform: capitalize;
}

a:focus {
  outline: none;
}

img {
  width: 100%;
  display: block;
  object-fit: cover;
}

h1,
h2,
h3 {
  margin-top: 0;
}

body {
  height: 100vh;
  width: 100%;
  display: grid;
  place-items: center;
  background-color: #0d192b;
  color: white;
  font-family: "Outfit", sans-serif;
  padding: 0 1.5rem;
}

hr {
  border: 0;
  height: 0.0625rem;
  background-color: #2f415b;
}

.card {
  padding: 1.125rem;
  border-radius: 0.9375rem;
  background-color: #14253d;
  max-width: 20.4375rem;
  display: grid;
  row-gap: 1rem;
}
@media screen and (min-width: 540px) {
  .card {
    max-width: 21.875rem;
  }
}
.card__img {
  border-radius: 0.5rem;
  height: 17.375rem;
}
@media screen and (min-width: 540px) {
  .card__img {
    height: 18.875rem;
  }
}
.card__img-content {
  position: relative;
}
.card__img-content-bg {
  display: grid;
  place-items: center;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 0.9375rem;
  transition: all 0.3s ease-in;
}
.card__img-content-bg::after {
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  transition: all 0.3s ease-in;
  border-radius: 0.5rem;
  content: "";
  position: absolute;
}
.card__img-content-bg:hover::after {
  background-color: #00fff7;
  opacity: 0.3;
}
.card__img-content-view {
  transition: all 0.3s ease-in;
  opacity: 0;
  width: 10%;
  height: 10%;
}
.card__img-content-bg:hover .card__img-content-view {
  opacity: 1;
}
.card__link {
  display: inline-block;
}
.card__link:hover {
  color: #00fff7;
}
.card__name {
  font-weight: 600;
  line-height: 1.75rem;
  margin-bottom: 0.75rem;
  font-size: 1.375rem;
}
.card__desc {
  color: #8bacda;
  line-height: 1.625rem;
  font-size: 1.125rem;
  font-family: 300;
}
.card__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card__price {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  gap: 0.375rem;
}
.card__cost {
  color: #00fff7;
  font-size: 1rem;
  line-height: 1.25rem;
}
.card__time {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  gap: 0.375rem;
}
.card__time-text {
  color: #8bacda;
  font-size: 1rem;
  line-height: 1.25rem;
}
.card__footer {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
}
.card__creator {
  height: 2.0625rem;
  width: 2.0625rem;
  border-radius: 50%;
}
.card__creator-info {
  display: inline-block;
  color: #8bacda;
}
.card__creator-name {
  color: white;
}
.card__creator-name:hover {
  color: #00fff7;
}

.name-link {
  color: green;
}